
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
* {
  margin: 0;
  padding: 0;
}
.clearfix:after{
  content:".";
  display:block;
  height:0;
  clear:both;
  visibility:hidden;
}
.clearfix{zoom:1;
}
.header {
  height: 90px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  overflow: hidden;
  background-color: #F5F5F5;
}
.top-bar {
/*  height: 50px;*/
  background-color: #D43D3D;
}
.top-nav-bar {
  border-bottom: 2px solid #f1f1f1;
}
.bar-l a, .bar-r a{
  display: block;
  width: 45px;
  height: 50px;
  line-height: 50px;
}
.bar-l a img {
  margin-left: -14px;
}
.bar-l a img, .bar-r a img{
  width: 28px;
  height: 28px;
}
.bar-m a {
  width: 96px;
  height: 50px;
  line-height: 50px;
}
.bar-m a img {
  height: 20px;
}
.bar-m a img.refresh {
height: 16px;
}



