
.article-title {
    font-size: 24px;
    line-height: 2.5rem;
    font-weight: 700;
    color: #222;
    text-align: left;
}
.article-author {
    margin-top: 12px;
}
.artcle-avatar {
    height: 34px;
    width: 34px;
    position: relative;
    float: left;
}
.artcle-avatar img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
}
.author-info {
    float: left;   
    padding-left: 8px;
}
.media-name {
    font-size: 14px;
    color:#222121;
}
.author-name {
     margin-bottom: 0px;
}
.author-name img {
    padding-left: 5px;
    height: 16px;
}
.publish-type {
    border: 1px solid #999;
    border-radius: 4px;
    font-size: 10px;
    -webkit-transform: scale(0.83);
    padding: 0 5px;
    color: #505050;
    margin-left: -5px;
}
.publish-time {
    font-size: 12px;
    line-height: 12px;
    display: inline-block;
    vertical-align: middle;
    color: #999;
    margin-left: 2px;
}
.attention {
    float: right;
    display: block;
    width: 72px;
    height: 30px;
    line-height: 30px;
    background-color: #2a90d7;   
    text-align: center;
    color: rgba(255,255,255,.96);
   /* -webkit-box-sizing: border-box;
    box-sizing: border-box;*/
    font-size: 14px;
    border-radius: 6px;
}
/*.article-content {
    position: relative;
    margin-top: 50px;
}*/
.big-img img {
    width: 100%;
    margin-top: 20px;
}
.img-title {
    font-size: 12px;
    color: #777;
    line-height: 16px;
    margin-top: 8px;
    margin-bottom: 15px;
}
.article-content-paragraph p {
    color: #333;
    text-align: left;
    margin-top: 24px;
    font-size: 18px;
    line-height: 2.5rem;
}
.article-content-paragraph p.img-caption {
    color: #777;
    text-align: center;
    font-size: 12px;
    line-height: 16px;
    margin-top: 8px;
}
.icon-hongbao {
    position: fixed;
    bottom: 40px;
    right: 20px;
}
.icon-hongbao img {
    width: 80px;
    height: 100px;
}



